import { Component } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { AppService } from './app.service';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { MatSnackBar } from '@angular/material/snack-bar';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  trackingForm = undefined;
  tNumber = '';
  constructor(private fb: FormBuilder) {
    this.trackingForm = this.fb.group({
      trackingNumber: ['', Validators.required],
    });
    this.readingMode = false
  }
 
  title = 'CustomerServicePortalMini';
  readingMode = false;
  url = "";
  download = function() {
    this.url="http://aka-internal.parago.com/rest/protected/SIMSService/GetSubmissionFilesByTrackingNumber/1/en_US/"+this.trackingForm.get("trackingNumber").value;
    this.readingMode = true;
    this.tNumber = this.trackingForm.get("trackingNumber").value;
  }

  reset = function () {
    this.readingMode = false;
    this.trackingForm.reset();
  }
}
